.container {
  display: block;
  width: 100%;
  max-width: 90vw;
  margin: 0 auto; }

@media only screen and (min-width: 425px) {
  .container-inset {
    max-width: 90vw; } }

@media only screen and (min-width: 768px) {
  .container-inset {
    max-width: 90vw; } }

@media only screen and (min-width: 992px) {
  .container-inset {
    max-width: 95vw; } }

@media only screen and (min-width: 1200px) {
  .container-inset {
    max-width: 95vw; } }

@media only screen and (min-width: 1600px) {
  .container-inset {
    max-width: 95vw; } }
